// Variables
$primary: "Aquatico", sans-serif;
$secondary: "Barlow", sans-serif;

$bp-mj: 420px;
$bp-sm: 768px;
$bp-md: 992px;
$bp-lg: 1280px;

/*===========
---GLOBALS---
===========*/

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-family: $secondary;
}

a {
  text-decoration: none;
  color: #1e95f7;
  text-shadow: 1px 3px 3px rgba(30, 149, 247, 0.25);
}

img {
  display: block;
  width: 100%;
  height: auto;
}

::-moz-selection { background-color:rgba(112, 213, 255, 0.25)}
::selection { background-color: rgba(112, 213, 255, 0.25) }
