.main-menu {
  width: 60%;
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: 8;
  display: flex;
  background: #fff;
  transition: transform 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: translateX(100%);

  &::before {
    content: "";
    position: absolute;
    height: 0;
    top: 0;
    left: -29px;
    z-index: 5;
    border-style: solid;
    border-width: 50vh 30px 50vh 0;
    border-color: transparent #fff transparent transparent;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -5;
    height: 100%;
    transform: translateX(0%);
    background: rgba(0, 0, 0, 0.5);
    background: linear-gradient(
      135deg,
      rgba(112, 213, 255, 0.5),
      rgba(0, 89, 188, 0.8)
    );
    opacity: 0;
    transition: transform 500ms ease, opacity 500ms ease;
    pointer-events: none;
  }

  @media screen and (max-width: 500px) {
    width: 80%;
  }


  &:hover {
    transform: translateX(calc(100% - 4rem));

    .menu-trigger {
      transform: translateY(-50%) rotate(-180deg);

      .menu-title {
        opacity: 1;
      }
    }
  }

  &.open {
    transform: translateX(10%);

    &:after {
      opacity: 1;
      transform: translateX(-100%);
    }

    .menu-trigger {
      span {
        &:nth-of-type(1) {
          margin-bottom: -11px;
          transform-origin: center center;
          transform: rotate(-45deg);
        }

        &:nth-of-type(2) {
          opacity: 0;
        }
        &:nth-of-type(3) {
          margin-top: -5px;
          transform-origin: center center;
          transform: rotate(45deg);
        }
      }

      .menu-title {
        top: 15px;
      }
    }
  }

  .menus {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    justify-content: space-between;
    width: 90%;
    padding: 17.5%;
    background-color: white;
  }

  .mcmenu {
    a {
      padding: 0 1rem 1.25rem;
      color: black;
      display: block;
      text-transform: uppercase;
      letter-spacing: 2px;
      cursor: pointer;
      font-size: 26px;
      text-shadow: none;
      position: relative;


      &:hover {
        &::after {
          opacity: 1;
        }

        &:first-child {
          color: tomato;
        }

        &:last-child {
          color: #e83e80;
        }
      }

      &::before {
        content: '✧';
        font-size: 14px;
        position: absolute;
        top: 7.5px;
        left: -5px;
      }

      &::after {
        font-size: 8px;
        position: absolute;
        letter-spacing: 1px;
        left: 15px;
        bottom: 2px;
        opacity: 0;
        transition: opacity 300ms ease;

      }

      &:first-child {
       
        &::after {
        content:'This will take you to my Instagram where most of my work is.';
          
        }
      }

      &:last-child {
        &::after {
        content:'This will take you to my Dribbble where most of my work is.';

        }
      }
    }
  }

  .mini-menu {
    a {
      padding: 1rem;
    }
  }

  ul.menu {
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;

    li {
      width: 80%;

      a {
        display: inline-block;
        font-size: 2rem;
        padding: 1rem;
        background: linear-gradient(90deg, #70d5ff, #0059bc);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.5s;
        transition-duration: 0.5s;

        &:before {
          content: "";
          position: absolute;
          z-index: -1;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: linear-gradient(270deg, #70d5ff, #2676d1);
          -webkit-transform: scaleX(0);
          transform: scaleX(0);
          -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
          -webkit-transition-property: transform;
          transition-property: transform;
          -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
          -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
        }
        &:hover,
        &:focus,
        &:active {
          color: white;
          background: white;
          -webkit-text-fill-color: white;
        }
        &:hover:before,
        &:focus:before,
        &:active:before {
          -webkit-transform: scaleX(1);
          transform: scaleX(1);
          -webkit-transition-timing-function: cubic-bezier(
            0.52,
            1.64,
            0.37,
            0.66
          );
          transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        }

        @media screen and (min-width: 362px) {
          padding: 1rem;
        }
      }
    }
  }
}

.menu-trigger {
  cursor: pointer;
  background: none;
  outline: none;
  border: none;
  padding: 0;
  z-index: 10;
  text-align: center;
  position: absolute;
  top: 50%;
  padding: 12px;
  transform: translateY(-50%);
  left: -60px;
  transition: all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  &:hover {
    span {
      box-shadow: 1px 3px 13px 0 rgba(30, 149, 247, 0.7);
    }
    .menu-title {
      text-shadow: 1px 3px 13px rgba(30, 149, 247, 0.7);
    }
}
 

  * {
    transition: all 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  span {
    display: block;
    width: 36px;
    margin-top: 6px;
    border-radius: 4px;
    height: 5px;
    background: linear-gradient(342deg, #70d5ff, #0059bc);
    box-shadow: 1px 3px 3px 0 rgba(30, 149, 247, 0.25);

    @media screen and (max-width: 450px) {
      width: 30px;
    }
  }

  .menu-title {
    position: absolute;
    transform: rotate(180deg);
    left: -45px;
    top: 25px;
    font-size: 13px;
    color: #2b6496;
    letter-spacing: 2px;
    font-family: "Aquatico";
    opacity: 0;
    transition: opacity 200ms ease;
  }
}

.social-media {
  width: 30px;
  position: fixed;
  top: 0px;
  left: 0px;
  background: #fff;
  height: 100vh;
  display: flex;
  z-index: 7;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  transition: width 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  &:hover {
    width: 90px;

    a {
      margin-left: 27px;
      margin-top: 2rem;
    }
  }

  a {
    margin-left: 12px;
    background: linear-gradient(135deg, #70d5ff, #0059bc);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    color: #52a2e4;
    padding: 5px 0;
    font-size: 26px;
    display: inline-block;
    border-radius: 50%;
    width: 36px;
    line-height: 36px;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(1) translateZ(0);
    transition: all 150ms ease;

    @media screen and (max-width: 450px) {
      font-size: 22px;
      width: 32px;
      margin-left: 14px;

      &:not(:first-child) {
        margin-top: 2rem;
      }
    }

    &:hover {
      color: white;
      -webkit-text-fill-color: white;
      transform: scale(1.3) translateZ(0);

      &:before {
        content: "";
        color: #000000;
        -webkit-text-fill-color: black;
        font-family: "Aquatico", sans-serif;
        position: absolute;
        top: -2rem;
        font-size: 8px;
        letter-spacing: 1px;
        padding: 7px 7px 4px;
        z-index: -999;
      }

      &:first-child {
        background: radial-gradient(
          circle at 33% 100%,
          #fed373 4%,
          #f15245 30%,
          #d92e7f 62%,
          #9b36b7 85%,
          #515ecf
        );

        &:before {
          content: "Instagram";
        }
      }

      &:nth-child(2) {
        background: #e83e80;

        &:before {
          content: "Dribbble";
        }
      }

      &:nth-child(3) {
        background: linear-gradient(
          to right,
          rgba(0, 99, 220, 1) 0%,
          rgba(0, 99, 220, 1) 50%,
          rgba(255, 0, 132, 1) 51%,
          rgba(255, 0, 132, 1) 100%
        );

        &:before {
          content: "Flickr";
        }
      }

      &:nth-child(4) {
        background: rgb(24, 24, 24);

        &:before {
          content: "Codepen";
        }
      }

      &:last-child {
        background: #552775;

        &:before {
          content: "Github";
        }
      }
    }
  }
}
